.grid {
  display: grid;
  grid-template-columns: repeat(2,1fr);
  column-gap: 20px;

  @include onTablet {
    grid-template-columns: repeat(6,80px);
    column-gap: 24px;
  }
  @include onDesktop {
    grid-template-columns: repeat(12,63px);
    column-gap: 24px;
  }

  &__desktop-tablet {
    display: block;

    @include onTablet {
      display: grid;
    }

    @include onDesktop {
      display: grid;
    }
  }

  
  &__item{

    @for $start from 1 through 6{
      @for $end from $start through 6 {
        &--tablet-#{$start}-#{$end}{
          @include onTablet{
          grid-column: #{$start} / #{$end+1};
          }
        }
      }
    }
    


    @for $start from 1 through 12{
      @for $end from $start  through 12 {
        &--desktop-#{$start}-#{$end}{
          @include onDesktop{
          grid-column: #{$start} / #{$end+1};
          }
        }
      }
    }
  }
}